@import 'styles/color.scss';

.btn {
  cursor: pointer;
  border-radius: 10px;
  background: white;
  border: 2px solid $button_add;
  text-align: center;
  color: $font-color;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 16px;
  margin: 40px 0px 10px 0px;
  line-height: 200%;

  &:hover {
    transform: scale(1.05);
    box-shadow: 0px 2px 0px $button_add;
  }
}
