@import 'styles/color.scss';

.btn {
    background-color: $main-color;
    border: none;
    box-shadow: 0px 8px 0px $font-color;
    cursor: pointer;
    border-radius: 10px;

    img {
        width: 50px;
    }

    &:hover {
        transform: scale(1.1);
        box-shadow: 0.5px 0.5px 5px black;
    }
}